'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import CartItem from '@/components/CartItem'
import CartSummary from '@/components/CartSummary'

// Mock cart data for now - will be replaced with actual cart hook
const mockCartItems = [
  {
    id: 1,
    name: 'Premium Cotton T-Shirt',
    price: 89,
    image: 'https://images.unsplash.com/photo-1521572163474-6864f9cf17ab?w=400&h=400&fit=crop',
    size: 'M',
    color: 'Black',
    quantity: 2
  },
  {
    id: 2,
    name: 'Classic Denim Jacket',
    price: 199,
    image: 'https://images.unsplash.com/photo-1544022613-e87ca75a784a?w=400&h=400&fit=crop',
    size: 'L',
    color: 'Dark Blue',
    quantity: 1
  }
]

export default function CartPage() {
  const [cartItems, setCartItems] = useState(mockCartItems)
  const [loading, setLoading] = useState(false)

  const updateQuantity = (id: number, size: string, color: string, newQuantity: number) => {
    if (newQuantity <= 0) {
      removeItem(id, size, color)
      return
    }

    setCartItems(prevItems =>
      prevItems.map(item =>
        item.id === id && item.size === size && item.color === color
          ? { ...item, quantity: newQuantity }
          : item
      )
    )
  }

  const removeItem = (id: number, size: string, color: string) => {
    setCartItems(prevItems =>
      prevItems.filter(item => !(item.id === id && item.size === size && item.color === color))
    )
  }

  const getTotalPrice = () => {
    return cartItems.reduce((total, item) => total + (item.price * item.quantity), 0)
  }

  const getTotalItems = () => {
    return cartItems.reduce((total, item) => total + item.quantity, 0)
  }

  if (cartItems.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-3xl font-bold text-gray-800 mb-4">Your cart is empty</h2>
          <p className="text-gray-600 mb-8">Looks like you haven't added anything to your cart yet.</p>
          <Link 
            href="/shop"
            className="bg-black text-white px-8 py-3 text-lg font-semibold hover:bg-gray-800 transition-colors"
          >
            Continue Shopping
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl md:text-4xl font-bold text-black mb-4">Shopping Cart</h1>
          <p className="text-lg text-gray-600">
            {getTotalItems()} {getTotalItems() === 1 ? 'item' : 'items'} in your cart
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Cart Items */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-lg shadow-sm">
              <div className="p-6">
                <h2 className="text-xl font-semibold text-black mb-6">Items</h2>
                <div className="space-y-6">
                  {cartItems.map((item, index) => (
                    <CartItem
                      key={`${item.id}-${item.size}-${item.color}`}
                      item={item}
                      onUpdateQuantity={updateQuantity}
                      onRemove={removeItem}
                      showDivider={index < cartItems.length - 1}
                    />
                  ))}
                </div>
              </div>
            </div>

            {/* Continue Shopping */}
            <div className="mt-6">
              <Link 
                href="/shop"
                className="inline-flex items-center text-black hover:text-gray-700 transition-colors"
              >
                <span className="mr-2">←</span>
                Continue Shopping
              </Link>
            </div>
          </div>

          {/* Cart Summary */}
          <div className="lg:col-span-1">
            <CartSummary
              items={cartItems}
              totalPrice={getTotalPrice()}
              totalItems={getTotalItems()}
            />
          </div>
        </div>
      </div>
    </div>
  )
}
