import Image from 'next/image'
import Link from 'next/link'

interface CartItemProps {
  item: {
    id: number
    name: string
    price: number
    image: string
    size: string
    color: string
    quantity: number
  }
  onUpdateQuantity: (id: number, size: string, color: string, quantity: number) => void
  onRemove: (id: number, size: string, color: string) => void
  showDivider?: boolean
}

export default function CartItem({ item, onUpdateQuantity, onRemove, showDivider = true }: CartItemProps) {
  const handleQuantityChange = (newQuantity: number) => {
    if (newQuantity >= 1) {
      onUpdateQuantity(item.id, item.size, item.color, newQuantity)
    }
  }

  const handleRemove = () => {
    onRemove(item.id, item.size, item.color)
  }

  return (
    <div>
      <div className="flex flex-col sm:flex-row gap-4">
        {/* Product Image */}
        <div className="w-full sm:w-24 h-24 relative bg-gray-100 rounded-lg overflow-hidden flex-shrink-0">
          <Link href={`/product/${item.id}`}>
            <Image
              src={item.image}
              alt={item.name}
              fill
              className="object-cover hover:scale-105 transition-transform"
            />
          </Link>
        </div>

        {/* Product Details */}
        <div className="flex-1 min-w-0">
          <div className="flex flex-col sm:flex-row sm:justify-between">
            <div className="flex-1">
              <Link 
                href={`/product/${item.id}`}
                className="text-lg font-semibold text-black hover:text-gray-700 transition-colors"
              >
                {item.name}
              </Link>
              <div className="mt-1 text-sm text-gray-600">
                <span>Size: {item.size}</span>
                <span className="mx-2">•</span>
                <span>Color: {item.color}</span>
              </div>
              <div className="mt-2 text-lg font-bold text-black">
                ${item.price}
              </div>
            </div>

            {/* Quantity and Remove */}
            <div className="flex items-center justify-between sm:justify-end sm:flex-col sm:items-end gap-4 mt-4 sm:mt-0">
              {/* Quantity Controls */}
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => handleQuantityChange(item.quantity - 1)}
                  className="w-8 h-8 border border-gray-300 flex items-center justify-center hover:bg-gray-100 transition-colors"
                  disabled={item.quantity <= 1}
                >
                  -
                </button>
                <span className="w-12 text-center font-medium">{item.quantity}</span>
                <button
                  onClick={() => handleQuantityChange(item.quantity + 1)}
                  className="w-8 h-8 border border-gray-300 flex items-center justify-center hover:bg-gray-100 transition-colors"
                >
                  +
                </button>
              </div>

              {/* Remove Button */}
              <button
                onClick={handleRemove}
                className="text-red-600 hover:text-red-800 text-sm font-medium transition-colors"
              >
                Remove
              </button>
            </div>
          </div>

          {/* Subtotal */}
          <div className="mt-4 text-right sm:text-left">
            <span className="text-sm text-gray-600">Subtotal: </span>
            <span className="font-semibold text-black">${(item.price * item.quantity).toFixed(2)}</span>
          </div>
        </div>
      </div>

      {/* Divider */}
      {showDivider && <div className="border-t border-gray-200 mt-6"></div>}
    </div>
  )
}
