import Link from 'next/link'

interface CartSummaryProps {
  items: Array<{
    id: number
    name: string
    price: number
    quantity: number
  }>
  totalPrice: number
  totalItems: number
}

export default function CartSummary({ items, totalPrice, totalItems }: CartSummaryProps) {
  const shipping = totalPrice > 100 ? 0 : 15
  const tax = totalPrice * 0.08 // 8% tax
  const finalTotal = totalPrice + shipping + tax

  return (
    <div className="bg-white rounded-lg shadow-sm p-6 sticky top-8">
      <h2 className="text-xl font-semibold text-black mb-6">Order Summary</h2>
      
      <div className="space-y-4">
        {/* Items Summary */}
        <div className="space-y-2">
          {items.map((item) => (
            <div key={`${item.id}-summary`} className="flex justify-between text-sm">
              <span className="text-gray-600">
                {item.name} × {item.quantity}
              </span>
              <span className="text-black font-medium">
                ${(item.price * item.quantity).toFixed(2)}
              </span>
            </div>
          ))}
        </div>

        <div className="border-t border-gray-200 pt-4 space-y-2">
          {/* Subtotal */}
          <div className="flex justify-between">
            <span className="text-gray-600">Subtotal ({totalItems} items)</span>
            <span className="text-black font-medium">${totalPrice.toFixed(2)}</span>
          </div>

          {/* Shipping */}
          <div className="flex justify-between">
            <span className="text-gray-600">Shipping</span>
            <span className="text-black font-medium">
              {shipping === 0 ? 'Free' : `$${shipping.toFixed(2)}`}
            </span>
          </div>
          {shipping === 0 && (
            <p className="text-xs text-green-600">Free shipping on orders over $100!</p>
          )}

          {/* Tax */}
          <div className="flex justify-between">
            <span className="text-gray-600">Tax</span>
            <span className="text-black font-medium">${tax.toFixed(2)}</span>
          </div>
        </div>

        {/* Total */}
        <div className="border-t border-gray-200 pt-4">
          <div className="flex justify-between">
            <span className="text-lg font-semibold text-black">Total</span>
            <span className="text-lg font-bold text-black">${finalTotal.toFixed(2)}</span>
          </div>
        </div>

        {/* Checkout Button */}
        <div className="pt-4">
          <Link
            href="/checkout"
            className="w-full bg-black text-white py-3 px-6 text-center font-semibold hover:bg-gray-800 transition-colors block"
          >
            Proceed to Checkout
          </Link>
        </div>

        {/* Continue Shopping */}
        <div className="pt-2">
          <Link
            href="/shop"
            className="w-full border-2 border-gray-300 text-gray-700 py-3 px-6 text-center font-semibold hover:border-gray-400 hover:text-black transition-colors block"
          >
            Continue Shopping
          </Link>
        </div>

        {/* Security Notice */}
        <div className="pt-4 text-xs text-gray-500 text-center">
          <p>Secure checkout with SSL encryption</p>
          <p className="mt-1">30-day return policy</p>
        </div>
      </div>
    </div>
  )
}
